.temprature {
    margin-top: -1%;
    text-align: center;
    font-size: 200%;
}

.min-max {
    text-align: center;
    margin-top: 1%;
    /* font-weight: 300; */
    font-size: 130%;
    color: #707070;
}

#weathercon {
    height: 55%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 200%;
}

.fas {
    font-size: 100%;
    -webkit-animation: fas-anime 2s linear infinite alternate;
            animation: fas-anime 2s linear infinite alternate;
    padding: 2%;
}

@-webkit-keyframes fas-anime {
    from {
        -webkit-transform: scale(0.9);
                transform: scale(0.9);
    }
    to {
        -webkit-transform: scale(1.2);
                transform: scale(1.2)
    }
}

@keyframes fas-anime {
    from {
        -webkit-transform: scale(0.9);
                transform: scale(0.9);
    }
    to {
        -webkit-transform: scale(1.2);
                transform: scale(1.2)
    }
}

@media (max-width: 600px) {
    .box {
        width: 90%;
        /* height: 80%; */
    }

    .wave {
        top: 85%;
    }

    #weathercon {
        font-size: 5em;
    }

    .info {
        font-size: 100%;
    }
}

@media (max-width: 300px) {
    .box {
        width: 100%;
    }

    .wave {
        top: 115%;
    }
}

body > span {
