}

.inputField {
    width: 50%;
    margin: auto;
    border-radius: 0.8rem;
    padding: 1rem 1rem;
    outline: none;
    font-size: 100%;
}

.errorMsg {
    text-align: center;
    font-size: 220%;
    margin: 15%;
}

